<template>
  <div class="gamer-gallery critical-component-clear">
    <div class="container container--wide">
      <swiper
        :auto-height="swiperSettings.autoHeight"
        :breakpoints="swiperSettings.breakpoints"
        :center-insufficient-slides="swiperSettings.centerInsufficientSlides"
        :loop="swiperSettings.loop"
        :modules="modules"
        :navigation="swiperSettings.navigation"
        :prevent-clicks="swiperSettings.preventClicks"
        :prevent-interaction-on-transition="swiperSettings.preventInteractionOnTransition"
        :slides-per-view="swiperSettings.slidesPerView"
        :space-between="16"
        :threshold="swiperSettings.threshold"
        class="gamer-gallery__carousel swiper-container"
      >
        <div
          :js-gamer-gallery="`prev-${liquid.sectionId}`"
          class="swiper-button-prev swiper-button-custom"
        >
          <icon-chevron-left />
        </div>

        <swiper-slide
          v-for="slide in slides"
          :key="slide.id"
          class="gamer-gallery__cell"
          @click="openGallery"
        >
          <responsive-image
            :image="{
              src: slide.settings.image
            }"
            block-class="gamer-gallery"
            min-max="310-310"
          />
        </swiper-slide>

        <div
          :js-gamer-gallery="`next-${liquid.sectionId}`"
          class="swiper-button-next swiper-button-custom"
        >
          <icon-chevron-right />
        </div>
      </swiper>
    </div>

    <overlay
      key="gamerGallery"
      :namespace="namespace"
      block-class="gamer-gallery-modal"
    >
      <template #body="{ isActive }">
        <gamer-gallery-modal
          :id="liquid.sectionId"
          :is-active="isActive"
          :slides="slides"
        />
      </template>
    </overlay>
  </div>
</template>

<script>

/**
 * Vue: Gamer gallery (gamer-gallery)
 * -----------------------------------------------------------------------------
 * Display up to 10 photos of a gaming influencer.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 * @param {Array} slides - Section blocks with images.
 */

import { mapActions } from 'vuex'

import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation } from 'swiper'

import ResponsiveImage from '~global/images/responsive-image'
import GamerGalleryModal from '~async/gamer-gallery-modal/gamer-gallery-modal'

import IconChevronRight from '~icons/direction/chevron/right.svg'
import IconChevronLeft from '~icons/direction/chevron/left.svg'


export default {
  name: 'GamerGallery',

  components: {
    GamerGalleryModal,
    IconChevronRight,
    IconChevronLeft,
    ResponsiveImage,
    Swiper,
    SwiperSlide,
  },

  props: {
    liquid: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },
    slides: {
      type: Array,
      default: () => ([]),
    },
  },

  setup() {
    return {
      modules: [Navigation],
    }
  },

  data() {
    return {
      swiperSettings: {
        autoHeight: false,
        breakpoints: {
          768: {
            spaceBetween: 24,
          },
        },
        centerInsufficientSlides: true,
        loop: false,
        navigation: {
          nextEl: `[js-gamer-gallery="next-${this.liquid.sectionId}"]`,
          prevEl: `[js-gamer-gallery="prev-${this.liquid.sectionId}"]`,
        },
        preventClicks: false,
        preventClicksPropagation: false,
        preventInteractionOnTransition: true,
        slidesPerView: 'auto',
        spaceBetween: 16,
        threshold: 5,
      },
    }
  },

  computed: {
    namespace() {
      return `gamerGallery-${this.liquid.sectionId}`
    },
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      closeOverlay: 'overlays/close',
      openOverlay: 'overlays/open',
    }),

    openGallery() {
      this.openOverlay({
        component: 'gamer-gallery-modal',
        ignoreDismissed: true,
        namespace: this.namespace,
      })
    },
  },
}
</script>

<style lang="scss">
@import './gamer-gallery';
</style>
